ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #333;
}

li {
	float: left;
}

li a {
	display: block;
	color: white;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
}

li a:hover {
	background-color: #111;
}

.bg {
	background-image: url("./imgs/beautiful-color-ui-gradients-backgrounds-endless-river.png");
	background-size: 100%;
	background-repeat: no-repeat;
	align-content: center;
	text-align: center;
}
